/* ================================
   Theme: Dog (light, friendly)
   No dark overlay — content stays readable
   Colors only — shapes from base.css
   ================================ */

:root.dog {
  --bg: #fff8e6; /* warm parchment */
  --surface: rgba(255, 255, 255, 0.98);
  --surface-contrast: #111318;

  --text: #111318;
  --text-muted: #5d626b;

  --chip-bg: rgba(255, 255, 255, 0.98);
  --chip-text: #111318;
  --chip-border: rgba(0,0,0,0.10);
  --chip-bg-active: #f3f7ff;
  --chip-text-active: #0b2a66;

  --accent: #ff7a3d;
  --accent-contrast: #ffffff;

  --hl-very: rgba(220, 53, 69, 0.20);
  --hl-slight: rgba(255, 193, 7, 0.22);
}

body.dog {
  background: var(--bg);
  color: var(--text);
  /* If you have a paw-print pattern, apply it to body as background-image,
     but do NOT add any dark/opacity overlay over content. */
}
