/* ================================
   Theme: Classic (light, neutral)
   Colors only — shapes from base.css
   ================================ */

:root.classic {
  --bg: #f7f9fc;
  --surface: #ffffff;
  --surface-contrast: #111318;

  --text: #111318;
  --text-muted: #5f6b7a;

  --chip-bg: #ffffff;
  --chip-text: #111318;
  --chip-border: rgba(0,0,0,0.10);
  --chip-bg-active: #eef3ff;
  --chip-text-active: #0b2a66;

  --accent: #2b6fff;
  --accent-contrast: #ffffff;

  /* Highlight tints tuned for light bg */
  --hl-very: rgba(220, 53, 69, 0.22);   /* red-ish */
  --hl-slight: rgba(255, 193, 7, 0.25); /* yellow */
}

body.classic {
  background: var(--bg);
  color: var(--text);
}
