/* ================================
   Theme: Cat (dark, playful warm)
   Colors only — shapes from base.css
   ================================ */

:root.cat {
  --bg: #0d0f12;
  --surface: rgba(18, 22, 28, 0.98);
  --surface-contrast: #f7fbff;

  --text: #f7fbff;
  --text-muted: #aab8c5;

  --chip-bg: rgba(28, 34, 44, 0.98);
  --chip-text: #f7fbff;
  --chip-border: rgba(255,255,255,0.10);
  --chip-bg-active: rgba(38, 46, 60, 0.98);
  --chip-text-active: #ffffff;

  --accent: #f1b14f;
  --accent-contrast: #0a0a0a;

  --hl-very: rgba(255, 88, 88, 0.30);
  --hl-slight: rgba(255, 196, 77, 0.32);
}

body.cat {
  background: var(--bg);
  color: var(--text);
  /* Background image? Put it on body, not above content. */
}
