/* ================================
   Theme: Arcade (dark, high contrast)
   Colors only — shapes from base.css
   ================================ */

:root.arcade {
  --bg: #0b0f17;
  --surface: #0f1420;
  --surface-contrast: #e8f0ff;

  --text: #e8f0ff;
  --text-muted: #9fb3c8;

  /* Opaque chip/surface colors for legibility on busy bgs */
  --chip-bg: #162133;
  --chip-text: #e8f0ff;
  --chip-border: rgba(255,255,255,0.12);
  --chip-bg-active: #1e2c45;
  --chip-text-active: #ffffff;

  --accent: #49d6ff;
  --accent-contrast: #0a0a0a;

  /* Highlight tints tuned for dark bg */
  --hl-very: rgba(255, 82, 82, 0.28);   /* red-ish */
  --hl-slight: rgba(255, 187, 66, 0.28);/* yellow */
}

body.arcade {
  background: var(--bg);
  color: var(--text);
  /* If you use a neon-grid background image, apply it to body,
     not as an overlay over content. */
}
